.french-section-5{
    position: relative;
    padding-top:10px;
    padding-bottom: 80px;
    background-color: #F2FBFF;
  }
  .french-section-5-heading{
      font-size: 45px;
      font-weight: 600;
      text-align: center;
      color: #323232;
  }
  
    
    .reviews{
      display: flex;
      flex-direction: row;
      flex-wrap: nowrap;
      overflow-x: scroll;
      overflow-y: hidden;
      scroll-behavior: smooth;
      /* overflow: -moz-scrollbars-none; 
      -ms-overflow-style: none;  */
    }
    .review-container{
      position: relative;
      min-width: 367px;
      max-width: 367px;
      height: 370px;
      border-radius: 5px;
      background-color: white;
      margin-left: 20px;
      margin-right: 20px;
      margin-top: 20px;
      margin-bottom: 20px;
      box-shadow: 0px 2px 10px 10px rgba(90, 195, 255, 0.2);
    }
    .reviewer-info{
      display: flex;
      position: absolute;
      bottom: 30px;
      left: 20px;
      color: #697694;
      align-items: center;
    }
    .reviewer-name-lvl{
      margin-left: 15px;
    }
    .reviewer-image{
      width: 85px;
    }
    .reviewer-name{
      font-size: 22px;
      font-weight: 700;
    }
    .reviewer-rating{
      margin-left: 20px;
      padding-top: 50px;
    }
    .reviewer-review{
      margin-left: 20px;
      margin-right: 20px;
      padding-top: 20px;
    }
    @media only screen and (max-width: 768px)
    {
      .french-section-5{
        padding-bottom: 20px;
      }
        .french-section-5-heading{
            font-size: 26px;
            font-weight: 500;
            text-align: center;
            margin-left: 15px;
            margin-right: 15px;
        }
    }
    @media only screen and (max-width: 620px)
    {
      /* .review-container{
        min-width: 227px;
        max-width: 227px;
        height: 370px;
        margin-left: 10px;
        margin-right: 10px;
        margin-top: 20px;
        margin-bottom: 20px;
      } */
      .reviewer-review{
        font-size: 14px;
        color: #697694;
      }
      .french-section-5 .left-btn{
        bottom: 30px;
        right: 110px;
        /* font-weight: 600; */
      }
      
      .french-section-5 .right-btn{
        bottom: 30px;
        right: 50px;
        
      }
    }