@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400;500;700&display=swap);
body{
  margin: 0;
  font-family: 'Ubuntu', sans-serif;
}

.App {
  text-align: center;
  max-width: 2200px;
  overflow-x: hidden;
  overflow-y: hidden;
  background-color: #323848;
  color: white;
}



.about-section{
    display: flex;
    margin-left: 5px;
    margin-right: 5px;
    padding: 10px;
    color: white;
}

.about-text-section{
    display: flex;
    flex:1 1;
    flex-direction: column;
    text-align: left;
    margin-left: 30px;
    margin-right: 30px;
}
.about-img-section{
    flex:1 1;
    min-width: 300px;
    min-height: 300px;
    background-image: url(/static/media/grp.f6c32b3a.jpg); 
    background-position: center;
    object-fit: cover;
    background-repeat: no-repeat;
    background-size: auto 100%;
}
.about-section-heading{
    font-size: 2.3rem;
    font-weight: 700;
}
.about-section-text{
    font-weight: 530;
    font-size: 1.3rem;
}
.about-section-text-btn{
    padding: 10px;
    width: 150px;
    font-weight: 700;
    transition: 0.3s;
    cursor: pointer;
    border: 2px solid #df9a2a;
    background-color: inherit;
    color: white;
}
.about-section-text-btn:hover{
    background-color: #df9a2a;
    color: black;
}
@media only screen and (max-width:1100px)
{
    .about-section{
        display: flex;
        flex-direction: column-reverse;
    }  
    .about-img-section{
        background-position: center;
        background-repeat: no-repeat;
        object-fit: contain;
        background-size: auto 100%;
    } 
}
.lawyer-hero{
    display: flex;
    flex-direction: row;
    padding-top: 40px;
    padding-bottom: 40px;
    height: 550px;
}
.lawyer-hero-section-1{
    flex: 1 1;
    padding-left: 80px;
    /* display: flex;
    flex-direction: column; */
}
.lawyer-hero-section-1-heading{
    margin-top: 90px;
    margin-bottom: 30px;
    font-size: 45px;
    text-align: left;
}
.lawyer-hero-section-1-sub-heading{
    font-size: 18px;
    text-align: left;
    color: #c8cddc;
    margin-bottom: 80px;
}
.lawyer-hero-section-1-btn{
    display: block;
    border: 1px solid #df9a2a;
    padding-top: 15px;
    padding-bottom: 15px;
    padding-left: 20px;
    padding-right: 20px;
    background-color: #323848;
    color: white;
    cursor: pointer;
    transition: 0.3s;
    max-width: 350px;
}
.lawyer-hero-section-1-btn:hover{
    background-color: #df9a2a;
    color: black;
    transition: 0.3s;
    font-weight: 600;
}
.lawyer-hero-section-2{
    flex: 1.4 1;
}
.lawyer-hero-section-3{
    display: flex;
    flex: 1 1;
}
.lawyer-hero-section-3-points{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.lawyer-hero-section-2-img{
    width: 100%;
    height: 100%;
}
.point{
    display: flex;
    flex-direction: row;
    text-align: left;
    margin-top: 25px;
    margin-bottom: 25px;
}
.point-img{
    width: 50px;
}
.icon-text-heading{
    font-size: 14px;
    color: #c8cddc;
}
.icon-text-sub-heading{
    font-size: 16px;
    color: white;
    font-weight: 550;
}
.navbar{
    display: flex;
    height: 80px;
    top: 0;
    left: 0;
    z-index: 9999;
    position: sticky;
    align-items: center;
    background-color: white;
}
.law-icon{
    width: 40px;
    height: 40px;
}
.navbar-icon{
    flex: 1 1;
    display: flex;
    align-items: center;
    margin-left: 10px;
    margin-right: 10px;
    margin-top: 10px;
}
.menu-items{
    flex: 1.5 1;
    display: flex;
    justify-content: flex-end;
    list-style: none;
    margin-right: 10px;
    font-weight: 500;
}
.navbar-text{
    margin-left: 5px;
    font-size: 2rem;
}
.items{
    background-color: black;
    color: white;
    padding: 10px 15px;
    border-top-right-radius: 12px;
    border-top-left-radius: 12px;
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px;
    margin-left: 10px;
    margin-right: 10px;
    font-size: 1.2rem;
    cursor: pointer;
}
.items:hover{
    transition-duration: 1s;
    background-color: white;
    color: black;
    border: 3px solid black;
}
@media only screen and (max-width: 768px) 
{
    .menu-icon{
        width: 30px;
        height: 90px;
        margin-right: 20px;
        cursor: pointer;
    }
    .menu-items-mobiles{
        position: absolute;
        background-color: white;
        height: 300px;
        width: 100%;
        top: 80px;
        left: 0;
    }
    .menu-items{
        display: flex;
        flex-direction: column;
        list-style: none;
        font-weight: 500;
    }
    .items{
        margin-top: 10px;
        margin-bottom: 10px;
        margin-right: 30px;
        font-size: 1.2rem;
        cursor: pointer;
       
    }
    
}
