.our-services{
    width: 100%;
}
.our-services-heading{
    font-size: 44px;
}
.our-services-cards{
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    margin-left: 20px;
    margin-right: 20px;
}
.our-services-card{
    position: relative;
    min-width: 300px;
    max-width: 350px;
    height: 400px;
    margin-top: 20px;
    margin-bottom: 20px;    
    transition: 0.3s;
    cursor: pointer;
    z-index: 1;
}
.our-services-card-heading{
    position: absolute;
    bottom: -100px;
    visibility: hidden;
    color: #FFF;
    font-size: 24px;
    font-weight: 600;
}
.our-services-card-text{
    position: absolute;
    bottom: -100px;
    visibility: hidden;
    color: #FFF;
}
.services-image{
    object-fit: cover;
    object-position: center;
    width: 100%;
    height: 100%;
    border-bottom-left-radius: 20px;
    border-top-right-radius: 20px;
}

.card-overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    visibility: none;
    transition: .5s ease;
    background-color: #393839;
    border-bottom-left-radius: 20px;
    border-top-right-radius: 20px;
}
.our-services-card:hover .card-overlay{
    opacity: 50%;
}
.our-services-card:hover .our-services-card-heading{
    bottom: 40px;
    left: 20px;
    visibility: visible;
    transition: 0.3s;
}
.our-services-card:hover .our-services-card-text{
    bottom: 20px;
    left: 20px;
    visibility: visible;
    transition: 0.3s
}
    
    

